<template>
  <div class="container">
    <div class="tips-container" v-if="!soNo && productTrackList && productTrackList.packageNum > 0">
      <div class="icon"></div>
      <span class="tips-text" v-if="from !== 'detail'">您的订单{{productTrackList ? productTrackList.packageNum : ''}}个包裹已发出</span>
      <span class="tips-text" v-else>您的订单已拆成{{productTrackList ? productTrackList.packageNum : ''}}个包裹发出</span>
    </div>
    <div :class="{'content': true,'content-margin': productTrackList && productTrackList.packageNum && productTrackList.packageNum > 0 }">
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <template #pulling><refreshLoading text="下拉刷新"/></template>
      <template #loosing><refreshLoading text="下拉刷新"/></template>
      <template #loading><refreshLoading text="加载中"/></template>
      <template #success><refreshLoading text="加载完成"/></template>
      <van-list
       v-if="productTrackList"
        v-model="loading"
        :finished="finished"
        :finishedText="''"
        @load="onLoad"
        offset="100"
        van-clearfix
        :immediate-check="false"
      >
      <div>
        <tracking-item v-for="(item, index) in productTrackList.cards" :key="item.expressNumber + index" :parentData="item"></tracking-item>
      </div>
      </van-list>
      <div v-if="finished && productTrackList && productTrackList.cards.length === 0" class="no-data">
        <div class="nodata-icon"> </div>
        <div class="no-bottom">当前订单暂无物流信息</div>
      </div>
    </van-pull-refresh>
    </div>
  </div>
</template>

<script>
/*
 * 接口配置文件
 */
import personalApi from '@/http/service/index'
import trackingItem from './components/tracking-item'
// const refreshLoading = () => import('@/components/refreshLoading')
import refreshLoading from '@/components/refreshLoading'

/*
 * 物流详情
 */
export default {
  name: 'trackingDetail',
  components: {
    trackingItem,
    refreshLoading,
  },
  props: {
    /**
     * 父组件传入数据源
     */
    parentData: {
      type: null,
      default: function () {
        return {}
      },
    },
  },
  data() {
    return {
      orderSn: '', // 订单号
      soNo: '', // 零售单号
      cOrderSn: '', // 网单号
      productTrackList: null,
      loading: false,
      finished: false,
      isLoading: false,
      from: '', // 跳转来源
      routerkey: null,
      reqParam: {
        orderSn: '',
        corderSn: '',
        pageNo: 1,
        pageSize: 5,
      }
    }
  },
  created() {
    this.soNo = this.$route.query.soNo
    if (this.soNo) {
      this.finished = true
    }
  },
  beforeRouteLeave(to, from, next) {
    let $content = document.querySelector('#app-warp'); // 列表的外层容器
    let scrollTop = $content ? $content.scrollTop : 0;
    from.meta.scrollTop = scrollTop;
    next();
  },
  mounted() {
    // if (this.routerkey && this.routerkey === this.$route.query.routerkey) {
    //   // console.log('activated scroll')
    //   let scrollTop = this.$route.meta.scrollTop;
    //   let $content = document.querySelector('#app-warp');
    //   if ($content) {
    //     this.$nextTick(() => {
    //       $content.scrollTop = scrollTop;
    //     })
    //   }
    // } else {
    // if (this.routerkey !== this.$route.query.routerkey) {
    // console.log('activated load')
    this.productTrackList = null
    this.reqParam.orderSn = this.$route.query.orderSn
    this.reqParam.corderSn = this.$route.query.cOrderSn
    this.from = this.$route.query.from
    const context = this;
    // 判断登陆状态，未登录便登陆
    if (context.$util.isHaierApp()) { // 智家
      context.$store.dispatch('handleUpAuth').then((res) => {
        console.log(res); // return true / false
        if (res) {
          // 获取第一页数据
          context.onRefresh();
        } else {
          this.$store.dispatch('doLogin')
        }
      });
    } else {
      // h5
      // 获取第一页数据
      context.onRefresh();
    }
    // }
    // this.routerkey = this.$route.query.routerkey
    // }
  },
  // mounted() {
  // const context = this;
  // 判断登陆状态，未登录便登陆
  // if (context.$util.isHaierApp()) { // 智家
  //   context.$store.dispatch('handleUpAuth').then((res) => {
  //     console.log(res); // return true / false
  //     if (res) {
  //       // 获取第一页数据
  //       context.onRefresh();
  //     } else {
  //       this.$store.dispatch('doLogin')
  //     }
  //   });
  // } else {
  //   // h5
  //   // 获取第一页数据
  //   context.onRefresh();
  // }
  // },
  methods: {
    /**
     * @description 初始化订单或者网单下的 物流追踪信息列表
    */
    initOrderProductTrackList() {
      const context = this;
      this.loading = true
      personalApi.orderProductTrackList(this.reqParam).then(res => {
        if (res.data.success) {
          if (context.productTrackList && context.reqParam.pageNo !== 1) {
            context.productTrackList.cards = context.productTrackList.cards.concat(res.data.data.cards)
          } else {
            context.productTrackList = res.data.data
          }
          context.reqParam.pageNo += 1;
          // 加载状态结束
          context.finished = false;
          context.loading = false;
          // 下啦刷新完成
          if (context.isLoading) {
            context.isLoading = false;
          }
          // 数据全部加载完成
          if ((context.productTrackList.cards.length >= res.data.data.count) || (!res.data.data.cards || (res.data.data.cards && res.data.data.cards.length === 0))) {
            context.finished = true;
          }
        }
      })
    },
    /**
     * @description 门店订单 发货单列表
    */
    initSoTrackList() {
      personalApi.getGoodsMsgList({ retailOrderCode: this.soNo }).then(res => {
        console.log('res')
        console.log(res)
        if (res.data.success) {
          let transObj = { cards: [] }
          let retData = res.data.data
          if (retData && retData.length > 0) {
            retData.forEach(i => {
              let cardItem = {
                expressCompany: '发货单号',
                expressNumber: i.deliveryOrderCode,
                logisticsType: 2,
                offline: true, // 标记是来自线下的订单
                newNode: {
                  nodeContent: i.nodeContents,
                  nodeId: '',
                  nodeName: '',
                  nodeTime: '',
                  sortNum: 0
                },
                nodeStatus: i.nodeStatus, // TODO 待确认
                orderProductInfos: []
              }
              i.goods.forEach(g => {
                let goodItem = {
                  corderSn: g.deliveryOrderCode,
                  defaultImageUrl: g.picUrl,
                  productFullName: g.productName,
                  productName: g.productName,
                  amount: g.amount
                }
                cardItem.orderProductInfos.push(goodItem)
              })
              transObj.cards.push(cardItem)
            })
          }
          // 下啦刷新完成
          if (this.isLoading) {
            this.isLoading = false;
          }
          console.log('transObj:')
          console.log(transObj)
          this.productTrackList = transObj
        }
      })
    },
    /**
     * @description 下拉刷新,下啦刷新同时清空现有数据，页码初始化1
     */
    onRefresh() {
      const context = this;
      context.reqParam.pageNo = 1;
      // 加载状态结束
      context.loading = false;
      context.finished = true;
      context.isLoading = true;
      if (context.soNo) {
      // 零售单号存在 代表门店订单
        context.initSoTrackList();
      } else {
        /**
          * @description 物流追踪信息列表
        */
        context.initOrderProductTrackList();
      }
    },

    /**
     * 页面上拉触底事件的处理函数，加载更多
     */
    onLoad() {
      // 门店订单 数据全部返回无下拉刷新
      if (this.soNo) return
      this.initOrderProductTrackList();
    },

  },
}
</script>

