    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding-left: 0.26rem;
    position: fixed;
    top: 0.92rem;
    left: 0;
    z-index: 5;
    .icon {
      // background: url('~@/assets/imgs/common/tips-icon-yellow.png') center center no-repeat;
      background: url('https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/common/tips-icon-yellow.png');
      background-size: cover;
      width: 0.32rem;
      height: 0.32rem;
      margin-right: 0.04rem;
    }
    .tips-text {
      font-size: 0.24rem;
      color: #ff8c25;
      line-height: 0.34rem;
    }
  }
  .content {
    height: 100%;
    padding-bottom: 0.24rem;
  }
  .content-margin{
    margin-top: 0.8rem;
  }
  ::v-deep .van-pull-refresh{
    height: 100%;
    overflow: unset;
  }
}
.no-data{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 1.85rem;
  height: 60vh;
  box-sizing: border-box;
}
.no-bottom{
  width: 7.5rem;
  display:flex;
  align-items: center;
  justify-content: center;
  margin-top: 0.32rem;
  color: #999999;
  font-size: .28rem;
}
.nodata-icon{
  height: 1.3rem;
  width: 1.28rem;
  background: url('~@/assets/imgs/orders/no-order.png');
  // background: url('https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/order/nodata.png');
  background-size: 1.28rem 1.3rem;
}
</style>
