<template>
  <div class="container" @click="toTrackInfo">
   <div class="left">
     <img class="thumb" :src="parentData.defaultImageUrl||'http://cdn02.ehaier.com/product/4e2fbaa934198c4c7a00001e.jpg'">
   </div>
   <div class="right">
     <span class="text">{{parentData.productFullName}}</span>
     <span class="amount" v-if="parentData && parentData.amount">x{{parentData.amount}}</span>
   </div>
  </div>
</template>

<script>

/*
 * 物流详情
*/
export default {
  name: 'product',
  props: {
    /**
     * 父组件传入数据源
     */
    parentData: {
      type: null,
      default: function () {
        return {}
      },
    },
    /**
     * 订单是否线下订单 线下订单：true
    */
    offline: {
      type: Boolean,
      default: function () {
        return false
      },
    }
  },
  data() {
    return {

    }
  },
  mounted() {
  },
  methods: {
    /**
     * @description 点击网单信息跳转物流跟踪页面
    */
    toTrackInfo() {
      if (this.offline) {
        // 线下订单 传入 发货单号
        this.$router.push({
          path: `/trackingOrders?soNo=${this.parentData.corderSn}`
        })
      } else {
        // 区分跳转地图页还是物流跟踪页逻辑  b2b2c并且 自营或者oco的 跳地图页，其他情况以及待确定/已取消状态的订单跳旧物流跟踪页
        if (
          this.parentData.opStatus !== 110 &&
          this.parentData.opStatus !== 0 &&
          this.parentData.opStatus !== 1 &&
          this.parentData.shippingMode === 'B2B2C' &&
        (this.parentData.stockType === 'WA' || this.parentData.stockType === 'OCO')) {
          this.$router.push({
            path: `/mapTrackingOrders?cOrderSn=${this.parentData.corderSn}&expressNumber=${this.parentData.expressNumber}&expressCompany=${this.parentData.expressCompany}`
          });
        } else {
          this.$router.push({
            path: `/trackingOrders?cOrderSn=${this.parentData.corderSn}&expressNumber=${this.parentData.expressNumber}&expressCompany=${this.parentData.expressCompany}`
          });
        }
      }
    }
  },
}
</script>

<style lang="scss" scoped>
.container {

.left{
  .thumb{
    width: 1.4rem;
    height: 1.4rem;
    border-radius: .14rem;
    margin: .26rem .24rem 0 .32rem;
  }
}
.right{
  padding-top: .4rem;
  padding-right: .24rem;
  width: 100%;
  position: relative;
  .amount{
    position: absolute;
    right: .5rem;
    bottom: .24rem;
    font-size: .24rem;
    font-family: PingFangSC, PingFangSC-Regular;
    font-weight: 400;
    color: #999999;
  }
  .text{
    font-size: .24rem;
    color: #999999;
    line-height: .26rem;
  }
}
}
</style>
