<template>
  <div class="container">
    <div class="item-content">
      <div class="header">
        <div class="status">
          <template v-if="parentData.nodeStatus">
              <div :class="{'icon-install':parentData.nodeStatus.toLowerCase() === 'install','icon-yixiadan':parentData.nodeStatus.toLowerCase() === 'order','icon-paisong':parentData.nodeStatus.toLowerCase() === 'deliver','icon-chulizhong':parentData.nodeStatus.toLowerCase() === 'depot','icon-yunshuzhong':parentData.nodeStatus.toLowerCase() === 'shipping','icon-yiqianshou':parentData.nodeStatus.toLowerCase() === 'sign','icon-yiquxiao':parentData.nodeStatus.toLowerCase() === 'cancel'}"></div>
              <span class="text" v-if="parentData.nodeStatus.toLowerCase() === 'order'">已下单</span>
