              <span class="text" v-if="parentData.nodeStatus.toLowerCase() === 'shipping'">运输中</span>
              <span class="text" v-if="parentData.nodeStatus.toLowerCase() === 'deliver'">配送中</span>
              <span class="text" v-if="parentData.nodeStatus.toLowerCase() === 'sign'">已签收</span>
              <span class="text" v-if="parentData.nodeStatus.toLowerCase() === 'cancel'">已取消</span>
              <span class="text" v-if="parentData.nodeStatus.toLowerCase() === 'install'">安装服务</span>
          </template>
        </div>
        <div class="express">
          <span class="express-content" v-if="parentData.logisticsType === 2">
            <span class="text">{{parentData.expressCompany}}：{{parentData.expressNumber}}</span>
            <div v-if="!parentData.offline" class="copy" @click="copy"></div>
          </span>
          <span class="express-content" v-if="parentData.logisticsType === 1">
            <span class="text">此商品由商家配送</span>
            <div class="self-icon"></div>
          </span>
        </div>
      </div>
      <div class="description">{{parentData.newNode.nodeContent}}</div>
      <div class="content">
        <template v-for="(item, index) in parentData.orderProductInfos">
           <div class="devider" :key="'devider'+item.corderSn + index"></div>
