</template>

<script>
import product from './product';
import ClipboardJS from 'clipboard'; // 复制插件
/*
 * 物流详情
*/
export default {
  name: 'trackingItem',
  components: {
    product
  },
  props: {
    /**
     * 父组件传入数据源
     */
    parentData: {
      type: null,
      default: function () {
        return {}
      },
    },
  },
  data() {
    return {

    }
  },
  mounted() {
  },
  methods: {
    /**
     * @description  复制运单号
    */
    copy() {
      let context = this
      const clipboard = new ClipboardJS('.copy', {
        text: function(trigger) {
          return context.parentData.expressNumber
        }
      });

      clipboard.on('success', () => {
        context.$toast.success('复制成功');
      });

      clipboard.on('error', () => {
        context.$toast.success('复制失败');
      });
    }
  },
}
</script>

